	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Mozilla 1.0","date":"05 Jun 2002","slug":"/2002-06-05-mozilla-10/","tags":"Y2002"},"html":"<p>\"Stick a fork in it. <a href=\"http://www.mozilla.org/\">It's done.</a>\"</p>"},"next":{"fields":{"title":"Sing the Praises of SSH","date":"04 Jun 2002","slug":"/2002-06-04-sing-the-praises-of-ssh/"}},"previous":{"fields":{"title":"World Cup Germany-Ireland","date":"05 Jun 2002","slug":"/2002-06-05-world-cup-germany-ireland/"}}},{"node":{"fields":{"title":"Sing the Praises of SSH","date":"04 Jun 2002","slug":"/2002-06-04-sing-the-praises-of-ssh/","tags":"Y2002"},"html":"<p>Brent Ashley sings <a href=\"http://brentashley.blogchat.com/archives/000369.html#000369\">The Praises of SSH</a> (in particular, <a href=\"http://www.openssh.org/\">OpenSSH</a>), and <a href=\"http://traumwind.tierpfad.de/blog/?detail=2002-06-03_23-07\">Martin</a> and <a href=\"http://inessential.com/?comments=1&amp;postid=2019\">Brent Simmons</a> say \"Amen\". I can only add that running <a href=\"http://www.rsync.org/\">rsync</a> (like scp, but faster and better) and <a href=\"http://www.cvshome.org/\">cvs</a> (version control) over ssh make it cooler yet, and that <a href=\"http://tramp.sourceforge.net/\">tramp for (x)emacs</a> and the <a href=\"http://www.vim.org/html/pi_netrw.html\">netrw</a> functions in <a href=\"http://www.vim.org/\">vim</a> enable you to locally edit files on your ssh server.</p>"},"next":{"fields":{"title":"Christopher Update, Without Pictures","date":"04 Jun 2002","slug":"/2002-06-04-christopher-update-without-pictures/"}},"previous":{"fields":{"title":"Mozilla 1.0","date":"05 Jun 2002","slug":"/2002-06-05-mozilla-10/"}}},{"node":{"fields":{"title":"Christopher Update, Without Pictures","date":"04 Jun 2002","slug":"/2002-06-04-christopher-update-without-pictures/","tags":"Y2002"},"html":"<p>I finally managed to get our Nikon CoolPix to the repair shop today. Hopefully in a couple of weeks we'll once again be able to provide anxious grandparents with Christopher pictures. His vocabulary is expanding by leaps and bounds, especially with respect to animals and heavy machinery. But airplanes are still tops on his list of favorite things.</p>\n<p>Meanwhile we're losing another colleague at work (they're buying a house in the country and she wants to have another baby), and we've elected an elections board, the first step on a long road in installing a worker's council (which seems to entail all of the bureaucracy and internal politics of a union, but without the dues).</p>"},"next":{"fields":{"title":"Blogroll OPML","date":"03 Jun 2002","slug":"/2002-06-03-blogroll-opml/"}},"previous":{"fields":{"title":"Sing the Praises of SSH","date":"04 Jun 2002","slug":"/2002-06-04-sing-the-praises-of-ssh/"}}}],"pathPrefix":"","first":false,"last":false,"index":889,"pageCount":1101}}

